import React, { FC, useCallback, useEffect } from "react";
import { useSelector } from "react-redux";
import { zodResolver } from "@hookform/resolvers/zod";
import { SubmitHandler, useForm } from "react-hook-form";

import { RootState } from "../../redux/store";
import { submitRequestForm } from "../../redux/Thunks/requestFormThunk";
import {
  clearFormRequest,
  RequestFormState,
  setField,
} from "../../redux/Slices/requestFormSlice";
import { IFormProps } from "../RequestForm/RequestForm";
import useAppDispatch from "../../Hooks/useAppDispatch";
import { RequestFormSchema } from "../../utils/zodSchems";
import { Accordion } from "../../ui-kit/Accordion/Accordion";
import { Button } from "../../ui-kit/Button/Button";
import { TitleH2 } from "../../ui-kit/Title/TitlesH2";
import { FormField, IRequestFormProps } from "../../ui-kit/Input/InputField";

import "react-slidedown/lib/slidedown.css";
import "./FAQ.scss";

const Faq: FC<IRequestFormProps> = () => {
  const dispatch = useAppDispatch();

  const { loading } = useSelector((state: RootState) => state.requestForm);

  const requestFormState = useSelector((state: RootState) => state.requestForm);

  const getRequestFormData = (state: RequestFormState) => ({
    userName: state.userName,
    phoneNumber: state.phoneNumber,
    email: state.email,
    comment: state.comment,
  });

  const {
    register,
    handleSubmit,
    formState: { errors },
    trigger,
    setValue,
    reset,
  } = useForm<IFormProps>({
    resolver: zodResolver(RequestFormSchema),
    defaultValues: getRequestFormData(requestFormState),
  });

  useEffect(() => {
    reset(getRequestFormData(requestFormState));
  }, [requestFormState, reset]);

  const onSubmit: SubmitHandler<IFormProps> = async () => {
    const requestFormData = getRequestFormData(requestFormState);
    dispatch(setField({ field: "requestFormData", value: requestFormData }));
    const isValid = await trigger();
    if (isValid) {
      const requestFormData = getRequestFormData(requestFormState);
      dispatch(submitRequestForm(requestFormData)).then(action => {
        if (submitRequestForm.fulfilled.match(action)) {
          setTimeout(() => {
            dispatch(clearFormRequest());
          }, 150);
        }
      });
    }
  };

  const handleFieldChange = useCallback(
    async (
      field: Exclude<
        keyof RequestFormState,
        "loading" | "error" | "requestFormData" | "notification"
      >,
      value: any,
    ) => {
      dispatch(setField({ field, value }));
      setValue(field, value);
      await trigger(field);
    },
    [dispatch, setValue, trigger],
  );
  return (
    <div className="Faq">
      <TitleH2 title={"Часто задаваемые вопросы"} color={"orange"} />
      <div className="Faq-Content">
        <div className="Faq-Content-Accordion">
          <Accordion title={"Как рассчитывается стоимость доставки?"}>
            <p>
              Стоимость доставки рассчитывается на основе веса, габаритов,
              расстояния и выбранного способа доставки.
            </p>
          </Accordion>
          <Accordion title={"Как происходит оплата за доставку груза?"}>
            <p>
              Оплата за доставку груза обычно производится через банковский
              перевод, электронные платежи или наличными при получении.
            </p>
          </Accordion>
          <Accordion
            title={"Как быстро оформить заказ на доставку из Китая в Россию?"}
          >
            <p>
              Заказ на доставку из Китая в Россию можно оформить быстро,
              заполнив необходимые формы на сайте и предоставив информацию о
              грузе.
            </p>
          </Accordion>
          <Accordion title={"Можно ли через вас оплатить товар поставщику?"}>
            <p>
              Да, мы предоставляем такую услугу, мы можем взять оплату товара в
              Китае на себя. Если у вас нет валютного счёта, то можете оплатить
              товар в рублях на наш счет в России.
            </p>
          </Accordion>{" "}
          <Accordion title={"В чём особенность КАРГО доставки из Китая?"}>
            <p>
              Особенность заключается в том, что компания берет на себя все
              аспекты логистики, включая покупку, упаковку и доставку товара.
            </p>
          </Accordion>
          <Accordion title={"Какой срок доставки из Китая?"}>
            <p>
              Срок доставки из Китая варьируется от 10 до 70 дней в зависимости
              от способа доставки и удаленности пункта назначения.
            </p>
          </Accordion>
        </div>

        <form onSubmit={handleSubmit(onSubmit)} className="Faq-Content-Form">
          <h3>Остались вопросы?</h3>
          <h4>Оставьте заявку и наш менеджер ответит вам в ближайшее время</h4>
          <div className="Faq-Content-Form-Fields">
            <div className="Faq-Content-Form-Field">
              <FormField
                type={"text"}
                placeholder={"Имя"}
                register={register}
                name={"userName"}
                error={errors.userName}
                onChange={e => handleFieldChange("userName", e.target.value)}
              />
            </div>

            <div className="Faq-Content-Form-Field">
              <FormField
                type={"phoneNumber"}
                placeholder={"Телефон"}
                register={register}
                name={"phoneNumber"}
                mask="+7 (999) 999-99-99"
                error={errors.phoneNumber}
                onChange={e => handleFieldChange("phoneNumber", e.target.value)}
              />
            </div>

            <div className="Faq-Content-Form-Field">
              <FormField
                type={"email"}
                placeholder={"E-mail"}
                register={register}
                name={"email"}
                error={errors.email}
                onChange={e => handleFieldChange("email", e.target.value)}
              />
            </div>

            <div className="Faq-Content-Form-Field">
              <FormField
                type={"textArea"}
                className=""
                placeholder={"Ваш вопрос"}
                register={register}
                name={"comment"}
                error={errors.comment}
                onChange={e => handleFieldChange("comment", e.target.value)}
              />
            </div>
          </div>
          <Button
            type={"submit"}
            text={loading ? "Отправка..." : "Отправить запрос"}
            icon={true}
            className="Faq-Content-Form-Button"
          />
        </form>
      </div>
    </div>
  );
};

export default Faq;
