import { z } from "zod";

export const FormSchema = z.object({
  category: z
    .string({
      required_error: "Обязательное поле",
      invalid_type_error: "Выберите категорию",
    })
    .min(1, { message: "Введите категорию" }),

  weight: z
    .number({
      required_error: "Обязательное поле",
      invalid_type_error: "Введите вес",
    })
    .min(0.5, { message: "Минимум 0.5кг" })
    .max(1000000, { message: "Максимум 1 млн кг" }),

  cost: z
    .number({
      required_error: "Обязательное поле",
      invalid_type_error: "Введите стоимость",
    })
    .min(1, { message: "Минимум 1$" })
    .max(10000000000, {
      message: "Максимум 10 млрд",
    }),

  volume: z
    .number({
      required_error: "Обязательное поле",
      invalid_type_error: "Введите объем",
    })
    .min(0.1, { message: "Минимум 0.1м3" })
    .max(999, { message: "Максимум 999 м3" }),
});

const nonEmptyString = z.string().refine(data => data.trim() !== "", {
  message: "Обязательное поле",
});

export const RequestFormSchema = z.object({
  email: z.string().email({ message: "Введите корректный E-mail" }),

  phoneNumber: nonEmptyString.refine(
    data =>
      /^(\+7|7|8)?[\s-]?\(?[1-9][0-9]{2}\)?[\s-]?[0-9]{3}[\s-]?[0-9]{2}[\s-]?[0-9]{2}$/.test(
        data,
      ),
    { message: "Введите номер" },
  ),

  userName: nonEmptyString.refine(
    data =>
      /(([\p{L}]{1}[\p{L}]{1,40})(\s[\p{L}]{1}[\p{L}]{1,40})?)\s([\p{L}]{1}[\p{L}]{1,40})/u.test(
        data,
      ),
    { message: "Введите Имя и Фамилию" },
  ),

  comment: z
    .string({
      required_error: "Обязательное поле",
      invalid_type_error: "Введите Комментарий",
    })
    .max(300, { message: "Максимальная длина 300 символов" })
    .nonempty("Обязательное поле"),
});
